import React from 'react';
import { useState } from 'react';
import { Sidebar } from './sidebar';
import { Header } from './header';
import { AddResourceModal } from '../add-resource-modal';
import { ResourceProvider } from '@/context/resource-context';

interface MainLayoutProps {
  children: React.ReactNode;
  onAddResource?: (data: { url: string; type: string; category: string }) => Promise<void>;
}

export function MainLayout({ children, onAddResource }: MainLayoutProps) {
  const [isModalOpen, setIsModalOpen] = useState(false);

  return (
    <ResourceProvider>
      <div className="flex h-screen bg-gray-50">
        <Sidebar />
        <div className="flex-1 flex flex-col">
          <Header onAddResource={() => setIsModalOpen(true)} />
          <main className="flex-1 p-6 overflow-auto">
            {children}
          </main>
        </div>
        <AddResourceModal
          open={isModalOpen}
          onOpenChange={setIsModalOpen}
          onSubmit={onAddResource || (async () => {})}
        />
      </div>
    </ResourceProvider>
  );
}